import { writable } from "svelte/store";

export default function (attr_string, vocabularyUrl) {
  const store = writable([]);
  vocabularyUrl = vocabularyUrl || "http://localhost:8081/Plone/@@getVocabulary?name=plone.app.vocabularies.Catalog&field=relatedItems&query=";

  store.request = async (method, path, params = null) => {
    let vocabQuery = `{"criteria":[{"i":"path","o":"plone.app.querystring.operation.string.path","v":"${path}::1"}],"sort_on":"getObjPositionInParent","sort_order":"ascending"}`;
    let attributes = attr_string.split(",").map((item) => item.trim());
    let attributesParam = "&attributes=" + JSON.stringify(attributes);
    //let batchParam = "&batch=" + JSON.stringify({ page: 1, size: 10 });
    let url = vocabularyUrl + vocabQuery + attributesParam; //+ batchParam;

    store.update((data) => {
      delete data.errors;
      data.loading = true;

      return data;
    });

    let headers = new Headers();
    headers.set("Content-type", "application/json");
    const body = params ? JSON.stringify(params) : undefined;

    const response = await fetch(url, { method, body, headers });
    const json = await response.json();

    if (response.ok) {
      return json;
    } else {
      store.update((data) => {
        data.loading = false;
        data.errors = json.errors;
        return data;
      });
    }
  };

  store.get = async (path, maxDepth) => {
    maxDepth = maxDepth || 3;
    console.log("maxDepth: ", maxDepth);
    console.log("path: ", path);
    let parts = path.split("/");
    console.log("parts: ", parts);
    const depth = parts.length >= maxDepth ? maxDepth : parts.length;
    let paths = [];

    let partsToShow = parts.slice(parts.length - depth, parts.length);
    let partsToHide = parts.slice(0, parts.length - depth);
    const pathPrefix = partsToHide.join("/");
    console.log("sliced parts: ", partsToShow);

    while (partsToShow.length > 0) {
      let sub_path = partsToShow.join("/");
      if (!sub_path.startsWith("/")) sub_path = "/" + sub_path;
      sub_path = pathPrefix + sub_path;
      console.log("sub_path: ",sub_path);
      const poped = partsToShow.pop();
      if ( poped === "") sub_path = "/";
      if(paths.indexOf(sub_path) === -1) paths.push(sub_path);
    }

    console.log("final paths: ", paths);
    console.log("partsToHide: ", partsToHide);

    let levels = [];
    for (var p of paths) {
      let level = await store.request("GET", p);
      level.path = p;
      levels = [level, ...levels];
      console.log("p: ", p);
    }
    store.set(levels);
    console.log(levels);
  };

  return store;
}
